.sliders {
  height: 1550px;
  width: 100%;
  overflow: hidden;
  position: relative;
  background: -webkit-linear-gradient(60deg, #ff4974, #ff5500);
  background: -o-linear-gradient(60deg, #ff4974, #ff5500);
  background: linear-gradient(30deg, #ff4974, #ff5500); }

.sliders .sloganimg {
  margin: auto;
  display: inline-block;
  position: absolute;
  left: 0;
  right: 0;
  top: 150px; }

@media (max-width: 991.98px) {
  .sliders .sloganimg {
    width: 350px; } }

@media (max-width: 767.98px) {
  .sliders .sloganimg {
    width: 300px;
    top: 120px; } }

.sliders .download {
  color: white;
  text-align: center;
  position: absolute;
  left: 0;
  top: 610px;
  width: 100%;
  z-index: 5;
  opacity: 0;
  -webkit-transform: translateY(50px);
  -ms-transform: translateY(50px);
  transform: translateY(50px);
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  -webkit-transition-delay: 3s;
  -o-transition-delay: 3s;
  transition-delay: 3s; }

.sliders .download a {
  width: 100%;
  display: inline-block;
  font-size: 18px;
  color: white; }

.sliders .download a:hover {
  text-decoration: none;
  color: #ff4974; }

.sliders .download .abtn {
  display: inline-block;
  margin: 10px 10px 0; }

.sliders .download .abtn a {
  display: block;
  border-radius: 25px;
  border: solid 1px white;
  padding: 5px 20px;
  background: #ff382b;
  width: 220px; }

.sliders .download .abtn a:hover {
  background-color: white; }

.sliders .download .apps {
  margin: 10px 10px 0;
  display: inline-block;
  color: white;
  position: relative; }

.sliders .download .apps span {
  text-align: center;
  padding: 5px 20px;
  display: block;
  width: 220px;
  font-size: 18px;
  position: relative;
  z-index: 1; }

@media (max-width: 991.98px) {
  .sliders .download .apps span {
    font-size: 16px; } }

.sliders .download .apps .abs {
  background: #ff382b;
  position: absolute;
  top: 0;
  left: 0;
  width: 220px;
  border-radius: 25px;
  border: solid 1px white;
  height: 38px;
  padding-top: 36px;
  overflow: hidden;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s; }

@media (max-width: 991.98px) {
  .sliders .download .apps .abs {
    height: 34px;
    padding-top: 34px; } }

.sliders .download .apps .abs span {
  padding: 3px 0 5px;
  line-height: 100%;
  font-size: 16px; }

.sliders .download .apps .abs a {
  font-size: 16px; }

.sliders .download .apps:hover .abs {
  overflow: hidden;
  height: auto;
  padding-top: 40px; }

@media (max-width: 767.98px) {
  .sliders .download .apps .abs {
    overflow: auto;
    height: auto;
    padding-top: 40px; } }

.sliders .download .apps a {
  border: 0;
  padding: 0;
  line-height: 36px;
  width: 180px;
  margin: 0 20px;
  border-radius: 0; }

.sliders .download .apps a:hover {
  background: none;
  color: white;
  text-decoration: underline; }

.sliders .download .apps a:first-child {
  border-bottom: solid 1px white; }

@media (max-width: 991.98px) {
  .sliders .download {
    top: 520px; }
  .sliders .download a {
    display: block;
    font-size: 16px;
    margin: 0 auto; } }

@media (max-width: 767.98px) {
  .sliders .download {
    top: 400px; } }

.sliders .sbg-cont {
  opacity: 0.5; }

.sliders .sbg-cont div {
  bottom: 150px; }

.sliders .bg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: url(../images/intro-gradient-bg.png) no-repeat center 300px; }

.sliders .spcont {
  z-index: 2;
  position: absolute;
  top: 0;
  left: 50%; }

.sliders .curve {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1; }

.sliders .curve img {
  position: absolute;
  width: 100%;
  min-width: 1919px;
  display: block;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  bottom: 0; }

.sliders .canv {
  top: 0;
  width: 100%;
  height: 1550px;
  overflow: hidden;
  position: relative;
  z-index: 2; }

.sliders .head-holder {
  top: 800px; }

.sliders .head-holder-anim h1 .title {
  opacity: 1;
  -webkit-transform: translateY(0px);
  -ms-transform: translateY(0px);
  transform: translateY(0px); }

.sliders .head-holder-anim h1 .desp {
  opacity: 1;
  -webkit-transition-delay: .3s;
  -o-transition-delay: .3s;
  transition-delay: .3s;
  -webkit-transform: translateY(0px);
  -ms-transform: translateY(0px);
  transform: translateY(0px); }

.sliders .head-holder-anim h1.hend:after {
  width: 70px;
  -webkit-transition-delay: 1s;
  -o-transition-delay: 1s;
  transition-delay: 1s; }

.sliders h1 {
  text-align: center;
  color: white;
  padding: 0 20px; }

.sliders h1 .title {
  font-size: 52px;
  line-height: 130px;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  opacity: 0;
  -webkit-transform: translateY(50px);
  -ms-transform: translateY(50px);
  transform: translateY(50px); }

.sliders h1 .title img {
  width: 100%;
  max-width: 396px;
  height: auto;
  margin-right: 20px; }

@media (max-width: 767.98px) {
  .sliders h1 .title img {
    margin: 0;
    width: 80%; } }

.sliders h1 .desp {
  display: inline-block;
  font-size: 22px;
  font-weight: 400;
  line-height: 36px;
  max-width: 650px;
  width: 100%;
  margin: 0 auto;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  opacity: 0;
  -webkit-transform: translateY(50px);
  -ms-transform: translateY(50px);
  transform: translateY(50px); }

@media (max-width: 767.98px) {
  .sliders h1 .desp {
    text-align: center;
    font-size: 16px;
    line-height: 30px; } }

.sphere {
  width: 1000px;
  height: 800px;
  position: absolute;
  top: 500px;
  left: calc(50% - 500px);
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  -webkit-perspective: 700px;
  perspective: 700px; }

@-webkit-keyframes rotate {
  to {
    -webkit-transform: rotateY(1turn) rotateX(2turn);
    transform: rotateY(1turn) rotateX(2turn); } }

@keyframes rotate {
  to {
    -webkit-transform: rotateY(1turn) rotateX(2turn);
    transform: rotateY(1turn) rotateX(2turn); } }



