.lang-en .advantage h2 {
  letter-spacing: 0; }

.lang-en .advantage .icon p {
  font-size: 18px;
  line-height: 22px; }

.advantage h2 {
  color: #f50;
  letter-spacing: 10px;
  font-weight: 700;
  font-size: 62px; }

@media (max-width: 991.98px) {
  .advantage h2 {
    font-size: 32px; } }

.advantage .anim * {
  opacity: 1;
  -webkit-transform: translateY(0);
  -ms-transform: translateY(0);
  transform: translateY(0); }

.advantage .bgcont {
  position: absolute;
  overflow: hidden;
  width: 100%;
  top: 50px; }

.advantage .bgcont img {
  margin-left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  min-width: 1920px;
  width: 100%; }

@media (max-width: 991.98px) {
  .advantage .bgcont img {
    min-width: 1100px; } }

@media (max-width: 767.98px) {
  .advantage .bgcont img {
    min-width: 1920px; } }

.advantage .icon {
  padding: 50px 40px;
  position: relative;
  color: white;
  text-align: center; }

@media (max-width: 991.98px) {
  .advantage .icon {
    padding: 20px; } }

@media (max-width: 767.98px) {
  .advantage .icon {
    padding: 15px 50px; } }

.advantage .icon .pattern {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 0%;
  height: auto;
  -webkit-transition: all .6s;
  -o-transition: all .6s;
  transition: all .6s;
  -webkit-transition-delay: .2s;
  -o-transition-delay: .2s;
  transition-delay: .2s;
  z-index: 1; }

.advantage .icon .bg {
  width: 0;
  -webkit-transition: all .6s;
  -o-transition: all .6s;
  transition: all .6s;
  margin: 50px 0; }

@media (max-width: 991.98px) {
  .advantage .icon .bg {
    margin: 20px 0; } }

.advantage .icon h6 {
  -webkit-transition: all .4s;
  -o-transition: all .4s;
  transition: all .4s;
  opacity: 0;
  font-weight: 700;
  font-size: 26px;
  -webkit-transition-delay: .35s;
  -o-transition-delay: .35s;
  transition-delay: .35s; }

@media (max-width: 991.98px) {
  .advantage .icon h6 {
    font-size: 22px; } }

.advantage .icon p {
  opacity: 0;
  -webkit-transform: translateY(30px);
  -ms-transform: translateY(30px);
  transform: translateY(30px);
  -webkit-transition: all .4s;
  -o-transition: all .4s;
  transition: all .4s;
  -webkit-transition-delay: .45s;
  -o-transition-delay: .45s;
  transition-delay: .45s;
  font-size: 20px; }

@media (max-width: 991.98px) {
  .advantage .icon p {
    margin: 0;
    font-size: 16px; } }

.advantage .icon.anim .pattern {
  width: 35%; }

@media (max-width: 991.98px) {
  .advantage .icon.anim .pattern {
    width: 30%; } }

.advantage .icon.anim .bg {
  width: 60%; }

@media (max-width: 991.98px) {
  .advantage .icon.anim .bg {
    width: 50%; } }

.advantage .icon.anim h6 {
  opacity: 1; }

.advantage .icon.anim p {
  opacity: 1;
  -webkit-transform: translateY(0);
  -ms-transform: translateY(0);
  transform: translateY(0); }

.advantage .slash-anim p.conclusion:after {
  width: 80px; }

@media (max-width: 991.98px) {
  .advantage .slash-anim p.conclusion:after {
    width: 40px; } }



