.lang-en header nav {
  font-size: 16px;
  line-height: 28px; }

.lang-en header nav li a {
  font-size: 16px;
  padding-left: 15px; }

.lang-en header nav li a:after {
  left: 15px; }

@media (max-width: 991.98px) {
  .lang-en header nav li a:after {
    left: 15px;
    display: none;
    content: none; } }

@media (min-width: 991.98px) {
  .lang-en header nav li:last-child a {
    padding: 0; }
  .lang-en header nav li.language {
    padding-left: 20px;
    font-size: 18px; } }

header {
  color: white;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  position: relative; }

header .menu-cont {
  height: 110px;
  position: fixed;
  z-index: 99;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  left: 0;
  right: 0; }

@media (max-width: 767.98px) {
  header .menu-cont {
    height: 80px; } }

header .menubg {
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  background-color: transparent;
  border-bottom: solid 1px transparent;
  position: absolute;
  left: 0;
  top: 0;
  position: fixed;
  height: 110px;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  left: 0;
  right: 0;
  z-index: 8; }

@media (max-width: 767.98px) {
  header .menubg {
    height: 80px; } }

header .bgactive {
  border-bottom: solid 1px #fff;
  background-color: #fe5a22; }

header .logo {
  width: 200px;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  position: absolute;
  top: 25px;
  left: 30px;
  z-index: 99; }

header .logo img {
  width: auto;
  height: 60px;
  position: absolute;
  top: 0;
  left: 0; }

@media (max-width: 767.98px) {
  header .logo {
    top: 20px; }
  header .logo img {
    height: 40px; } }

header .logo .red {
  opacity: 0;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s; }

header .switchVsM {
  position: absolute;
  right: 0;
  z-index: 9;
  display: none;
  top: 130px; }

header .switchVsM a span {
  color: #fff;
  display: inline-block;
  padding: 8px;
  background: #0a55d5 url(../images/vsa.png) no-repeat 90% 11px;
  background-size: 8px; }
  @media (max-width: 991.98px) {
    header .switchVsM a span {
      background: #0a55d5; } }

header .switchVsM a span:hover {
  text-decoration: none; }

header .switchVsM a span.spe {
  background-position: 80% 32px; }

@media (max-width: 991.98px) {
  header .switchVsM {
    display: block; } }

@media (max-width: 767.98px) {
  header .switchVsM {
    top: 100px; } }

header nav {
  margin: 0;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  left: 0;
  right: 0;
  padding-right: 30px;
  font-size: 18px;
  position: fixed;
  z-index: 2000;
  top: 35px; }

header nav ul {
  float: right;
  position: relative;
  z-index: 3000; }

header nav li {
  display: inline-block;
  overflow: hidden; }

header nav li a {
  color: white;
  padding-left: 30px;
  display: inline-block;
  position: relative;
  height: 50px;
  line-height: 50px; }

header nav li a:after {
  content: '';
  display: inline-block;
  position: absolute;
  top: 35px;
  left: 30px;
  height: 3px;
  width: 0;
  -webkit-transition: width .3s;
  -o-transition: width .3s;
  transition: width .3s;
  background-color: white; }

header nav li a:hover {
  color: white;
  text-decoration: none; }

header nav li a:hover:after {
  width: 100%; }

header nav li .langlink {
  padding: 0 !important; }

header nav li .langlink:after {
  display: none;
  content: none; }

header nav li.selected a {
  font-weight: 700; }

header nav li.selected a:after {
  width: 100%; }

@media (max-width: 991.98px) {
  header nav li.selected a:after {
    width: 0; } }

header nav li.selected a:hover {
  text-decoration: none; }

header nav li.language {
  padding-left: 30px; }

header nav li.language:after {
  content: "|";
  display: inline-block;
  padding: 0 20px; }

header nav li.switchVs {
  margin-left: 20px; }

header nav li.switchVs a {
  font-size: 16px;
  padding: 0 40px 0 15px !important;
  background: #0a55d5 url(../images/vsa.png) no-repeat 90% center;
  background-size: 8px; }

header nav li.switchVs a:hover:after {
  width: 0; }

header .menu-responsive {
  top: 45px;
  right: 30px; }

@media (max-width: 767.98px) {
  header .menu-responsive {
    top: 30px; } }

header .menu-button {
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
  width: 30px;
  height: 30px;
  position: relative;
  display: none;
  background-color: transparent;
  cursor: pointer; }

header .menu-button span {
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  display: block;
  height: 1px;
  width: 30px;
  margin-top: 10px;
  background-color: #fff; }

header .menu-button:before, header .menu-button:after {
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
  content: '';
  height: 1px;
  background-color: #fff;
  display: block;
  position: absolute;
  width: 30px;
  left: 0; }

header .menu-button:before {
  top: -10px; }

header .menu-button:after {
  top: 10px; }

@media (max-width: 991.98px) {
  header.responsive-header {
    max-width: 100% !important;
    margin: 0;
    padding: 0 25px; }
  header .menu-button {
    display: block; }
  header nav {
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
    left: 100%;
    background-color: white;
    width: 100%;
    height: 100%;
    z-index: 9;
    overflow: hidden;
    top: 0;
    right: 0; }
  header nav ul {
    display: block;
    position: absolute;
    width: 80%;
    margin: auto;
    left: 0;
    right: 0;
    bottom: 20px; }
  header nav li {
    color: #1e1e1e;
    display: block;
    border-bottom: solid 1px #f50;
    text-align: left; }
  header nav li a {
    display: inline-block;
    color: #1e1e1e;
    padding: 12px 20px;
    font-size: 18px; }
  header nav li a.langlink {
    font-size: 16px;
    padding: 20px 40px; }
  header nav li a.selected {
    color: #f50;
    font-weight: 700; }
  header nav li.language {
    border-bottom: 0;
    padding-left: 0; }
  header nav li.language:after {
    content: "";
    padding: 0; }
  header nav li.language a.langlink {
    padding-bottom: 0; }
  header nav li.switchVs {
    display: none; }
  header.menuactive .logo .red {
    opacity: 1; }
  header.menuactive .menu-button span {
    background-color: #f50;
    -webkit-transform: rotateZ(45deg);
    -ms-transform: rotate(45deg);
    transform: rotateZ(45deg); }
  header.menuactive .menu-button:before {
    opacity: 0; }
  header.menuactive .menu-button:after {
    top: 0;
    background-color: #f50;
    -webkit-transform: rotateZ(-45deg);
    -ms-transform: rotate(-45deg);
    transform: rotateZ(-45deg); }
  header.menuactive nav {
    left: 0;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s; } }



