.lang-en .team h2 {
  letter-spacing: 0; }

.team h2 {
  font-family: "DINPro-Bold";
  font-size: 62px;
  letter-spacing: 6px;
  margin: 20px 0 90px; }

@media (max-width: 991.98px) {
  .team h2 {
    font-size: 32px;
    margin-bottom: 50px; } }

.team .member {
  padding: 0 60px;
  margin-bottom: 80px; }

@media (max-width: 991.98px) {
  .team .member {
    padding: 0 30px; } }

.team .member .portrait {
  position: relative;
  text-align: center;
  margin-bottom: 30px; }

.team .member .portrait .photo {
  width: 56%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  z-index: 1; }

.team .member .portrait .dec {
  width: 70%; }

@media (max-width: 991.98px) {
  .team .member .portrait {
    width: 50%;
    margin: 0 auto 20px; }
  .team .member .portrait .photo {
    width: 80%; }
  .team .member .portrait .dec {
    width: 100%; } }

.team .member .tname {
  font-family: "DINPro-Bold";
  font-size: 22px;
  letter-spacing: 2px;
  text-align: center; }

.team .member .trole {
  font-size: 14px;
  margin-bottom: 10px;
  color: #2a2a2a;
  text-align: center; }

.team .member .tposition {
  font-size: 16px;
  margin-bottom: 20px;
  text-align: center; }

.team .member p {
  font-size: 14px;
  line-height: 24px;
  color: #5d5d5d;
  word-wrap: break-word;
  word-break: break-all;
  text-align: center; }

footer {
  padding: 600px 0 0;
  background: #f50;
  color: white;
  background: url(../images/footerbg.png) no-repeat center bottom;
  min-height: 740px; }

@media (max-width: 991.98px) {
  footer {
    padding: 300px 0 30px; } }

@media (max-width: 767.98px) {
  footer {
    background-position: center top;
    min-height: 440px; } }

footer .copyright {
  margin-bottom: 20px; }

footer .copyright img {
  width: 140px;
  margin: 0 10px 10px 0; }

@media (max-width: 991.98px) {
  footer .copyright img {
    width: 110px; } }

footer .copyright p {
  font-size: 14px; }

@media (max-width: 991.98px) {
  footer .copyright p {
    display: inline;
    font-size: 8px; } }

footer .partner img {
  max-width: 100%;
  margin-bottom: 20px; }



